    box-shadow: 1px 2px 3px 0px rgba(0, 0, 0, 0.10);
    border-radius: 6px;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}

.shopping-cart .title {
    height: 60px;
    border-bottom: 1px solid #E1E8EE;
    padding: 20px 30px;
    color: #5E6977;
    font-size: 18px;
    font-weight: 400;
}

.shopping-cart .item {
    padding: 20px 30px;
    height: 120px;
    display: -webkit-flex;
    display: flex;
}

.shopping-cart .item:nth-child(3) {
    border-top: 1px solid #E1E8EE;
    border-bottom: 1px solid #E1E8EE;
}

.shopping-cart .buttons {
    position: relative;
    padding-top: 30px;
    margin-right: 60px;
}

.shopping-cart>.delete-btn, .like-btn {
    display: inline-block;
    Cursor: pointer;
}

.shopping-cart .delete-btn {
    width: 18px;
    height: 17px;
}

.shopping-cart .like-btn {
    position: absolute;
    top: 9px;
    left: 15px;
    background: url('https://firebasestorage.googleapis.com/v0/b/moonpillows-676c2.appspot.com/o/images%2Ftwitter-heart.png?alt=media&token=acdf45b8-a57b-45b4-a8cd-9ef3598da7f3');
    width: 60px;
    height: 60px;
    background-size: 2900%;
    background-repeat: no-repeat;
}
.shopping-cart .like-btn:hover {
    -webkit-animation-name: animate;
            animation-name: animate;
    -webkit-animation-duration: .8s;
            animation-duration: .8s;
    -webkit-animation-iteration-count: 1;
            animation-iteration-count: 1;
    -webkit-animation-timing-function: steps(28);
            animation-timing-function: steps(28);
    -webkit-animation-fill-mode: forwards;
            animation-fill-mode: forwards;
}

